<template>
  <div id="second-bg" style="position: relative">
    <div id="bg" v-show="!show"></div>
    <img
      id="play"
      @click="playVideo"
      src="../../assets/background/ico_play.svg"
      alt=""
    />
    <img id="txt" src="../../assets/background/txt/txt_movie_wide.svg" alt="" />
    <div id="mask" v-show="show"></div>
    <div id="vid" v-show="show">
      <img
        id="btn1"
        @click="closeVideo"
        src="../../assets/background/img-insert/close.png"
        alt=""
      />
      <video ref="theVid" controls src="../../assets/video/02.mp4"></video>
    </div>
  </div>
</template>

<script>
export default {
  name: "TheVideo",
  data() {
    return {
      show: false,
    };
  },
  methods: {
    playVideo() {
      this.show = true;
    },
    closeVideo() {
      this.show = false;
      this.$refs.theVid.pause();
    },
  },
};
</script>

<style scoped>
