  width: 50px;
  height: 50px;
  z-index: 10;
}

#btn1:hover {
  cursor: pointer;
}

#mask {
  position: absolute;
  height: 100%;
  width: 100%;
  opacity: 0.6;
  z-index: 0;
  background-color: black;
}

#play:hover {
  cursor: pointer;
}
#second-bg {
  position: relative;
  height: 800px;
  background-image: url("../../assets/background/sakura-tree2.png");
