<template>
  <div class="center">
    <!-- 头部 -->
    <BlogHeader :isShow="[isShow[0], isShow[1]]"></BlogHeader>

    <!--过渡图片  -->
    <BlogTransit></BlogTransit>

    <!-- 第二张长背景 -->
    <BlogSecondBg></BlogSecondBg>

    <!-- 人物 -->
    <TheCharacter :isShow="isShow[2]"></TheCharacter>

    <!-- 樱花背景 -->
    <BlogThirdBg></BlogThirdBg>

    <!-- 蓝天背景 -->
    <div id="blueSky" class="bg2"></div>

    <!-- 樱花背景 -->
    <div
      class="bg1"
      :style="{ backgroundPositionY: $store.getters.theScroH + 'px' }"
    ></div>

    <!-- 页脚部分 -->
    <footer>
      <!-- 花圈 -->
      <div class="footer"></div>
    </footer>
  </div>
</template>

<script>
// import "@/assets/JS/sakura";
// import "@/assets/JS/mousemove.js";
import "@/assets/CSS/animate.css";
// import "@/assets/CSS/blog.css";
import TheCharacter from "./content/TheCharacter.vue";
import BlogHeader from "./content/BlogHeader.vue";
import BlogTransit from "./content/BlogTransit";
import BlogSecondBg from "./content/BlogSecondBg";
import BlogThirdBg from "./content/BlogThirdBg";
export default {
  name: "MyHead",
  components: {
    TheCharacter,
    BlogHeader,
    BlogTransit,
    BlogSecondBg,
    BlogThirdBg,
  },
  data() {
    return {
      isShow: [false, false, false, false, false], //公告,故事,人物组件显示
      username: "",
      password: "",
    };
  },
  methods: {
    //监听滚动方法
    handleScroll() {
      // this.scroH = document.documentElement.scrollTop;
      this.$store.dispatch("change", document.documentElement.scrollTop);
    },
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll, true);
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.$data.username = from.params.username;
      vm.$data.password = from.params.password;
    });
  },
  watch: {
    "$store.state.scroH"() {
      // this.$store.dispatch("change", this.scroH);
      if (this.$store.state.scroH > 100) {
        this.isShow[0] = true;
      } else {
        this.isShow[0] = false;
      }
      if (this.$store.state.scroH > 300) {
        this.isShow[1] = true;
      } else {
        this.isShow[1] = false;
      }
      if (this.$store.state.scroH > 900) {
        this.isShow[2] = true;
      } else {
        this.isShow[2] = false;
      }
    },
  },
};
</script>

<style scoped>
#blueSky {
  background-image: url(../assets/background/space03.png);
  background-attachment: fixed;
  height: 200px;
}
</style>