        style="
          height: 20rem;
          width: 53.8%;
          position: absolute;
          left: -30%;
          top: 5rem;
          border: 2px solid pink;
          border-radius: 2px;
          box-shadow: 0px 0px 5px 2px hotpink;
        "
      />
    </transition>

    <transition name="showRight">
      <img
        class="enlarge"
        v-if="isShow"
        @click="showImg(1)"
        :src="img[1]"
