          position: absolute;
          height: 20rem;
          width: 53.8%;
          left: -30%;
          top: 35rem;
          border: 2px solid yellow;
          border-radius: 2px;
          box-shadow: 0px 0px 5px 2px yellowgreen;
        "
      />
    </transition>

    <transition name="showLeftBlur">
      <img
        class="enlarge"
        v-if="isShow"
        @click="showImg(2)"
        :src="img[2]"
        alt=""
        style="
          position: absolute;
          height: 20rem;
          width: 53.8%;
          top: 18rem;
          right: -42rem;
          border: 2px solid skyblue;
          border-radius: 2px;
          box-shadow: 0px 0px 5px 2px skyblue;
