        style="
          width: 69.2%;
          height: 525px;
          position: absolute;
          top: 5rem;
          right: -10rem;
        "
      />
    </transition>
    <div id="mask" v-show="show"></div>
    <img v-show="show" :src="img[num]" alt="" id="img1" class="img" />
    <img
      id="btn"
      @click="closeImg"
      v-show="show"
