<script>
import "@/assets/CSS/animate.css";
export default {
  name: "TheStory",
  data() {
    return {
      show: false,
      num: 0,
      img: [
        require("../../assets/background/story-img2.png"),
        require("../../assets/background/story-img1.png"),
        require("../../assets/background/img-insert/ETC_e002a.png"),
      ],
    };
  },
  methods: {
    showImg(num) {
      this.show = true;
      this.num = num;
    },
    closeImg() {
      this.show = false;
    },
  },
  props: {
    isShow: { typeof: Boolean },
  },
};
</script>

