<template>
  <!-- 樱花树和人物定位 -->
  <div id="sakuraTree" class="bg2">
    <!-- atri-logo -->
    <transition name="showRight">
      <img
        v-if="isShow"
        id="atri-logo"
        src="../../assets/background/logo.svg"
        alt=""
        style="
          position: absolute;
          left: 50px;
          top: -200px;
          width: 400px;
          height: 150px;
        "
      />
    </transition>

    <!-- 人物 -->

    <transition name="fadeInSlow">
      <img
        id="atri-img1"
        v-if="isShow"
        src="../../assets/background/atri.png"
        alt=""
        style="
          width: 900px;
          height: 810px;
          position: absolute;
          left: 300px;
          size: 100%;
          top: -600px;
        "
      />
    </transition>

    <!-- 文字部分 -->
    <template v-if="isShow">
      <div id="atri-txt">
        <!-- 右一文字 -->
        <img
          :class="imgAnimation.showLeft"
          id="atri-txt1"
          src="../../assets/background/txt/txt_1.svg"
          alt=""
          style="
            position: absolute;
            width: 200px;
            height: 400px;
            top: -520px;
            left: 850px;
          "
        />
        <!-- 右二文字 -->
        <img
          :class="imgAnimation.showRight"
          id="atri-txt2"
          src="../../assets/background/txt/txt_2.svg"
          alt=""
          style="
            position: absolute;
            width: 200px;
            height: 400px;
            top: -420px;
            left: 400px;
          "
        />
        <!-- 左一文字 -->
        <img
          :class="imgAnimation.showLeft"
          id="atri-txt3"
          src="../../assets/background/txt/txt_3.svg"
          alt=""
          style="
            position: absolute;
            width: 200px;
            height: 400px;
            top: -650px;
            left: 280px;
            filter: blur(3px);
          "
        />
        <!-- 左二文字 -->
        <img
          :class="imgAnimation.showRight"
          id="atri-txt4"
          src="../../assets/background/txt/txt_4.svg"
          alt=""
          style="
            position: absolute;
            width: 200px;
            height: 400px;
            top: -550px;
            left: 950px;
            filter: blur(3px);
          "
        />
      </div>
    </template>

    <!-- 樱花树上人物 -->
    <img
      style="
        width: 450px;
        height: 636px;
        position: absolute;
        top: -335px;
        left: 1090px;
      "
      src="../../assets/background/白花1.png"
      alt=""
    />
  </div>
</template>

<script>
import "@/assets/CSS/animate.css";

export default {
  name: "TheCharacter",
  props: ["isShow"],
  data() {
    return {
      imgAnimation: {
        showLeft: {
          showLeft: true,
        },
        showLeftSlow: {
          showLeftSlow: true,
        },
        fadeIn: {
          fadeIn: true,
        },
        fadeInSlow: {
          fadeInSlow: true,
        },
        showRight: {
          showRight: true,
        },
        showBottom: {
          showBottom: true,
        },
        showBottomBlur: {
          showBottomBlur: true,
        },
        showTop: {
          showTop: true,
        },
      },
    };
  },
};
</script>

<style>
#sakuraTree {
  background-image: url(../../assets/background/sakuraTree.png);
  height: 300px;
  position: relative;
}
</style>